<template>
  <div class="rebate-order">
    <img class="no-orderlist" v-if="lists.length == 0" src="//qianka.b0.upaiyun.com/images/54f41fdd65734161a0212cebe24fa35d.png" alt="">
    <scroller ref="scroller" class="order-scroll" height="100%" v-if="lists.length > 0" :onInfinite="infinite" noDataText="没有 更多数据">
      <div class="tips" v-if="showTips"><span class="font-regular">订单将在确认收货后1小时内显示，超时请&nbsp;<label @touchend="fowardToComplain()" class="cffa">导入订单</label></span></div>
      <div class="orderLists" v-for="(orders, index) in lists" :key="index">
        <div class="order-item">
          <div class="date">{{orders.date}}</div>
          <div class="desc" v-for="(order, index) in orders.orders" :key="order.order_sn">
            <div class="title" style="-webkit-box-orient: vertical;">{{order.item_title}}</div>
            <div class="price-numbers">
              <div class="price"><span class="font-10">￥</span>{{parseFloat(order.pay_price/order.item_num).toFixed(2)}}</div>
              <div class="numbers">x{{order.item_num}}</div>
            </div>
            <div class="commission"><span v-if="showPlus" class="plus">+</span><span class="font-20">{{order.commission}}</span><span> 集分宝</span>
              <div class="type">
                <span class="daifan" v-if="order.order_status === 0">预计{{format(order.estimate_time)}}到账</span>
                <span v-else-if="order.order_status === 1">已返利</span>
                <span v-else>已退货</span>
              </div>
            </div>
            <div class="order-info">
              <div class="order-num">订单号 <div class="right">{{order.order_sn}}</div></div>
              <div class="order-created_at">跟单时间 <div class="right">{{order.created_at}}</div></div>
            </div>
          </div>
        </div>
      </div>
    </scroller>
  </div>
</template>
<style lang="scss">
  @import '../../../scss/mixins';

  .rebate-order {
    width: 100%;
    height: 100%;
    position: relative;

    .no-orderlist {
      width: 222px;
      height: 248px;
      display: inline-block;
      position: absolute;
      top: 50%;
      left: 50%;
      margin-top: -124px;
      margin-left: -111px;
    }

    .order-scroll {
      .tips {
        margin: 20px auto;
        width: 343px;
        height: 40px;
        line-height: 40px;
        background: #FFF8EE;
        text-align: center;

        span {
          font-size: 12px;
          color: #3A3A3A;
          line-height: 17px;
        }

        .cffa {
          color: #FB800C;
          font-size: 12px;
          text-decoration: underline;
          padding: 12px 0;
        }
      }

      ._v-content {
        padding-bottom: 70px;

        .orderLists {
          .order-item {
            font-family: PingFangSC-Regular;
            padding: 0px 16px;
            margin-top: 20px;

            .date {
              font-size: 20px;
              color: #DDDDDD;
              line-height: 28px;
              margin-bottom: 4px;
            }

            .desc {
              position: relative;
              padding: 12px 0px 20px 0px;

              .title {
                font-size: 14px;
                color: #3A3A3A;
                line-height: 22px;
                width: 259px;
                height: 44px;
                margin-bottom: 6px;
                overflow: hidden;
                text-overflow: ellipsis;
                display: -webkit-box;
                -webkit-box-orient: vertical;
                -webkit-line-clamp: 2;
              }

              .price-numbers {
                width: 84px;
                position: absolute;
                right: 0px;
                top: 14px;
                text-align: right;

                .price {
                  font-size: 14px;
                  color: #3A3A3A;
                  line-height: 20px !important;
                  margin-bottom: 2px;

                  .font-10 {
                    font-size: 10px;
                  }
                }

                .numbers {
                  font-family: PingFangSC-Light;
                  font-size: 12px;
                  color: #B5B5B5;
                  line-height: 17px;
                }
              }

              .commission {
                font-size: 12px;
                line-height: 28px;
                height: 28px;
                box-sizing: border-box;
                color: #FB800C;
                margin-bottom: 10px;

                .plus {
                  margin-right: 2px;
                }

                .font-20 {
                  font-size: 20px;
                }

                .color-desc {
                  color: #FFA70F;
                }
              }

              .type {
                float: right;
                font-size: 12px;
                color: #3A3A3A;
                line-height: 17px;
                padding-top: 8px;

                span.daifan {
                  color: #4A90E2;
                }
              }

              .type.daifan {
                color: #B5B5B5;
              }

              .order-info {
                font-family: PingFangSC-Light;
                font-size: 12px;
                color: #B5B5B5;

                .order-num {
                  line-height: 17px;
                  margin-bottom: 2px;

                  .right {
                    float: right;
                  }
                }

                .order-created_at {
                  line-height: 17px;

                  .right {
                    float: right;
                  }
                }

                .order-estimate_time {
                  line-height: 17px;
                  margin-bottom: 2px;

                  .right {
                    float: right;
                  }
                }

                .order-product_from {
                  line-height: 17px;

                  .right {
                    float: right;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
</style>
<script>
  export default {
    props: {
      infinite: Function,
      lists: Array,
      showTips: Boolean,
      showPlus: {
        type: Boolean,
        default: true
      }
    },

    mounted () {
      $bus.$on('scrollTotop', _ => {
        this.$refs.scroller.scrollTo(0, 0, true)
      })
    },

    methods: {
      fowardToComplain () {
        $router.forward('/rebate/appeal')
      },

      format (dateString) {
        let month = dateString.substr(5, 2)
        if (month.charAt(0) === '0') {
          month = month.substr(1, 1)
        }
        let day = dateString.substr(8, 2)
        if (day.charAt(0) === '0') {
          day = day.substr(1, 1)
        }
        return `${month}月${day}日`
      }
    }
  }
</script>



