<template>
	<div class="page" v-nav="{hideNavbar: true}">
		 <AnimateHeader
      bg-color="#FFA70F"
      menu-bg-color="#FB940C"
      color="#FFFFFF"
      title="我的订单"
      :show-back-button="true"
      :on-back-button-click="back"
      :animate="false"
    >
    </AnimateHeader>

		<div class="page-content">
      <!-- <div class="tst"><button @click="forwardTo()">ss</button></div> -->
      <tab-layout :links="tabLinks" :bar-width="barWidth" bg-color="#FFF"
      :on-tab-click="onTabClick" use-content-slot="true" :has-new="shensuHasNew"></tab-layout>
      <div class="tips" v-if="activeIndex == 0 && daifanLists.length ==0"><span class="font-regular">订单将在确认收货后1小时内显示，超时请&nbsp;<label @click="fowardToComplain()" class="cffa">导入订单</label></span></div>
      <list v-if="loaded && activeIndex == 0" :lists="daifanLists" class="infiniteDaifan" :infinite="infiniteDaifan" :showTips="true" :showPlus="false"></list>
      <list v-if="loaded && activeIndex == 1" :lists="yifanLists" class="infiniteYifan" :infinite="infiniteYifan"></list>
      <list v-if="loaded && activeIndex == 2" :lists="yituiLists" class="infiniteYitui" :infinite="infiniteYitui"></list>
      <Complain :lists="appealLists" :on-tab-click="onTabClick" v-if="activeIndex == 3"></Complain>
		</div>
	</div>
</template>
<style lang="scss" scoped>
  .page-content {
    padding-top: 50px;
    background-color: #fff;

    .tst {
      position: fixed;
      z-index: 55;
    }

    .tips {
      position: absolute;
      top: 116px;
      left: 50%;
      margin-left: -171px;
      width: 343px;
      height: 40px;
      line-height: 40px;
      background: #FFF8EE;
      text-align: center;
      z-index: 55;

      span {
        font-size: 12px;
        color: #3A3A3A;
        line-height: 17px;
      }

      .cffa {
        color: #FFA70F;
        font-size: 12px;
        text-decoration: underline;
        padding: 12px 0;
      }
    }
  }
</style>
<script>
import AnimateHeader from '@/widget/animate-header/Index.vue'
import TabLayout from './TabLayout'
import List from './List'
import Complain from './Complain'
import {REBATE_COMPLAIN_LIST} from 'constants'

export default {
  components: {
    AnimateHeader,
    TabLayout,
    List,
    Complain
  },

  data () {
    return {
      barWidth: 271,
      tabLinks: [
        {
          to: { path: '/rebate/orderlist/daifan' },
          text: '待返利'
        },
        {
          to: { path: '/rebate/orderlist/yifan' },
          text: '已返利'
        },
        {
          to: { path: '/rebate/orderlist/yitui' },
          text: '已退货'
        },
        {
          to: { path: '/rebate/orderlist/shensu' },
          text: '订单比对'
        }
      ],
      activeIndex: 0,
      loaded: false,
      daifanLists: [],
      yifanLists: [],
      yituiLists: [],
      appealLists: [],
      daifanOffset: '',
      yifanOffset: '',
      yituiOffset: '',
      noMoreDaifan: true,
      noMoreYifan: true,
      noMoreYitui: true,
      type: 'daifan',
      PAGE_SIZE: 10,
      shensuHasNew: false
    }
  },

  mounted () {
    if (this.$route.name === 'fanli_orderlist') {
      this.tabLinks = [
        {
          to: { path: '/fanli/orderlist/daifan' },
          text: '待返利'
        },
        {
          to: { path: '/fanli/orderlist/yifan' },
          text: '已返利'
        },
        {
          to: { path: '/fanli/orderlist/yitui' },
          text: '已退货'
        }
      ]
      return
    }
    this.type = this.$route.params.type
    this.type === 'daifan' ? this.activeIndex = 0 : (this.type === 'yifan' ? this.activeIndex = 1 : (this.type === 'yitui' ? this.activeIndex = 2 : this.activeIndex = 3))
    this.fetchData()
  },

  methods: {
    fetchData () {
      $loading.show('加载中...')
      $api.jifenbaoUserOrderAll()
        .then(res => res.data && res.data.payload)
        .then((payload) => {
          // let payload = result.payload
          // console.log(payload)
          let daifanLists = payload.wait
          let yifanLists = payload.complete
          let yituiLists = payload.refurn
          let appealLists = payload.complain
          this.loaded = true

          _.each(daifanLists, (date, i) => {
            if (i === daifanLists.length - 1) {
              _.each(date.orders, (o, j) => {
                if (j === date.orders.length - 1) {
                  this.daifanOffset = o.id
                }
              })
            }
          })
          _.each(yifanLists, (date, i) => {
            if (i === yifanLists.length - 1) {
              _.each(date.orders, (o, j) => {
                if (j === date.orders.length - 1) {
                  this.yifanOffset = o.id
                }
              })
            }
          })
          _.each(yituiLists, (date, i) => {
            if (i === yituiLists.length - 1) {
              _.each(date.orders, (o, j) => {
                if (j === date.orders.length - 1) {
                  this.yituiOffset = o.id
                }
              })
            }
          })

          this.daifanLists = daifanLists
          this.yifanLists = yifanLists
          this.yituiLists = yituiLists
          this.appealLists = appealLists
          // 红点是否显示，数据比对
          if (this.type === 'shensu') {
            this.syncAppealLists()
          }
          let localAppealList = $storage.get(REBATE_COMPLAIN_LIST) || []
          _.each(this.appealLists, (complain) => {
            _.each(complain.orders, (order) => {
              let orderId = order.id + ':' + order.order_sn
              if (localAppealList.indexOf(orderId) === -1) {
                this.shensuHasNew = true
              }
            })
          })

          setTimeout($loading.hide, 300)
          setTimeout(() => {
            this.noMoreDaifan = this._numberLists(daifanLists) < this.PAGE_SIZE
            this.noMoreYifan = this._numberLists(yifanLists) < this.PAGE_SIZE
            this.noMoreYitui = this._numberLists(yituiLists) < this.PAGE_SIZE
          }, 1500)
        })
    },

    infiniteDaifan (done) {
      if (this.noMoreDaifan) {
        done()
        return
      }

      this._list(0, this.daifanOffset).then(result => {
        setTimeout(() => {
          this.noMoreDaifan = this._numberLists(result) === 0 || this._numberLists(result) < this.PAGE_SIZE

          if (this._numberLists(result) === 0) {
            done()
            return
          } else {
            // 如果下一页第一天 等于 当前页最后一天的日期 把下一页第一天的数据拼接到当前页最后一天
            this._format(result, this.daifanLists)
            this.daifanLists = this.daifanLists.concat(result)
            done()
          }
        }, 1500)
      })
    },

    infiniteYifan (done) {
      if (this.noMoreYifan) {
        done()
        return
      }

      this._list(1, this.yifanOffset).then(result => {
        setTimeout(() => {
          this.noMoreYifan = this._numberLists(result) === 0 || this._numberLists(result) < this.PAGE_SIZE
          if (this._numberLists(result) === 0) {
            done()
            return
          } else {
            this._format(result, this.yifanLists)
            this.yifanLists = this.yifanLists.concat(result)
            done()
          }
        }, 1500)
      })
    },

    infiniteYitui (done) {
      if (this.noMoreYitui) {
        done()
        return
      }

      this._list(2, this.yituiOffset).then(result => {
        setTimeout(() => {
          this.noMoreYitui = this._numberLists(result) === 0 || this._numberLists(result) < this.PAGE_SIZE

          if (this._numberLists(result) === 0) {
            done()
            return
          } else {
            this._format(result, this.yituiLists)
            this.yituiLists = this.yituiLists.concat(result)
            done()
          }
        }, 1500)
      })
    },

    _list (type, offset) {
      return $api
        .jifenbaoUserOrder(type, offset)
        .then(res => res.data && res.data.payload)
        .then(payload => {
          let orderLists = payload
          let tmp

          _.each(orderLists, (date, i) => {
            if (i === orderLists.length - 1) {
              _.each(date.orders, (o, j) => {
                if (j === date.orders.length - 1) {
                  tmp = o.id
                }
              })
            }
          })

          if (type === 0) {
            this.daifanOffset = tmp
          }
          if (type === 1) {
            this.yifanOffset = tmp
          }
          if (type === 2) {
            this.yituiOffset = tmp
          }
          return orderLists
        })
    },

    // 如果下一页第一天 等于 当前页最后一天的日期 把下一页第一天的数据拼接到当前页最后一天
    _format (newPage, currentPage) {
      let firstdate = newPage[0].date
      let lastIndex = currentPage.length - 1
      let lastDate = currentPage[lastIndex].date
      if (firstdate === lastDate) {
        currentPage[lastIndex].orders = currentPage[lastIndex].orders.concat(newPage[0].orders)
        newPage.splice(0, 1)
      }
    },

    _numberLists (lists) {
      let num = 0
      _.each(lists, (a, i) => {
        num += a.orders.length
      })
      return num
    },
    back () {
      let backRoute = this.$route.name.split('_')[0]
      $router.back('/' + backRoute)
    },
    fowardToComplain () {
      $router.forward('/rebate/appeal')
    },
    syncAppealLists () {
      let appealLists = []
      _.each(this.appealLists, (complain) => {
        _.each(complain.orders, (order) => {
          let orderId = order.id + ':' + order.order_sn
          appealLists.push(orderId)
        })
      })
      $storage.set(REBATE_COMPLAIN_LIST, appealLists)
    },
    onTabClick (index) {
      this.activeIndex = index
      if (index === 3) {
        this.syncAppealLists()
        this.shensuHasNew = false
      }
    }
  }
}
</script>



