<template>
  <div class="complain">
    <scroller class="complain-scroll" height="100%">
      <div class="tips"><span class="font-regular">比对成功的订单将自动添加到&nbsp;<span>待返利</span>&nbsp;中</span></div>
      <div v-if="lists.length > 0" class="complain-list" v-for="(orders,index) in lists" :id="index">
        <div class="complain-item">
          <div class="date">{{orders.date}}</div>
          <div v-for="(order,index) in orders.orders" :key="order.order_sn" class="info clearfix">
            <div class="order-sn">
              <span>订单号 {{order.order_sn}}</span>
              <span :class="order.check_status === 0 ? 'c4a9' :(order.check_status === 1 ? 'c3a3' : 'cef4')">{{order.check_status === 0 ? '等待比对' :(order.check_status === 1 ? '比对成功' : '比对失败')}}</span>
            </div>
            <div v-if="order.check_status == 0" class="estimate-time"><span>预计处理时间</span><span>{{order.estimate_time}}前</span></div>
            <div v-if="order.check_status !== 0" class="estimate-time"><span>处理完成时间</span><span>{{order.check_day}}</span></div>

          </div>
        </div>
      </div>
    </scroller>
  </div>
</template>
<style lang="scss">
  @import "../../../scss/mixins";

  .complain {
    position: relative;
    height: 100%;
    width: 100%;
    overflow: auto;

    .complain-scroll {
      padding: 19px 16px 70px;
    }

    ._v-content {
      padding-bottom: 70px;
    }

    .tips {
      width: 343px;
      height: 40px;
      line-height: 40px;
      background: #FFF8EE;
      text-align: center;
      margin-bottom: 30px;

      span {
        font-size: 12px;
        color: #3A3A3A;
        line-height: 17px;

        span {
          color: #FB800C;
        }
      }
    }

    .complain-list {
      margin-bottom: 50px;
    }

    .complain-item {
      .date {
        font-size: 20px;
        color: #DDDDDD;
        margin-bottom: 16px;

        @include font-regular();
      }

      .info {
        height: 45px;
        margin-bottom: 30px;

        .order-sn {
          margin-bottom: 8px;

          span {
            font-size: 14px;
            color: #3A3A3A;
          }

          span:nth-child(2) {
            float: right;
          }

          .c4a9 {
            color: #4A90E2;
          }

          .c3a3 {
            color: #3A3A3A;
          }

          .cef4 {
            color: #EF4F2A;
          }

          @include font-regular();
        }
      }

      .estimate-time {
        span {
          font-size: 12px;
          color: #B5B5B5;
        }

        span:nth-child(2) {
          float: right;
        }
      }
    }
  }
</style>
<script>
  export default {
    props: {
      lists: Array,
      forwarddaifan: Function,
      onTabClick: Function
    },

    methods: {
      forwardToDaifan () {
        $router.forward('/rebate/orderlist/daifan')
      },

      onClick (index) {
        if (this.onTabClick) this.onTabClick(index)
        $router.forward('/rebate/orderlist/daifan')
      }
    }
  }
</script>




