<template>
  <div class="layout" :style="{backgroundColor: bgColor}">
    <div class="credit-bar bar-subheade">
      <div class="button-bar" :style="{minWidth: barWidth + 'px'}">
        <router-link v-for="(link, index) in links" :key="index" :to="link.to"
          class="button font-regular" @click.native="onClick(index)"
          >
          <span class="label-container" :class="{numbered:numbered}"> <span class="number"><span v-text="index + 1"></span></span> <span v-text="link.text"></span> </span>
          <i v-if="index === 3&&hasNew" class="red-dot"></i>
        </router-link>
      </div>
    </div>
    <!-- sub routes -->
    <router-view v-if="useContentSlot == 'false'"></router-view>

    <!-- content slot -->
    <slot></slot>
  </div>
</template>
<style lang="scss" scoped>
  @import '../../../scss/variables';

  .button-bar > .button:first-child {
    border-radius: 0;
  }

  .button-bar > .button:last-child {
    border-radius: 0;
  }

  .layout {
    .credit-bar {
      width: 100%;

      .button-bar {
        margin: 0 auto;
        min-width: 270px;
        border-radius: 0 0 0 0;
        flex: 0;
        -webkit-box-flex: 0;

        .label-container {
          position: relative;

          &.numbered {
            left: 15px;

            .number {
              display: block;
            }
          }

          .number {
            display: none;
            border-radius: 15px;
            position: absolute;
            padding: 0;
            width: 15px;
            vertical-align: bottom;
            height: 15px;
            top: 2px;
            right: 100%;
            margin-right: 5px;
            border: 1px solid;

            span {
              position: absolute;
              top: -14px;
              width: 100%;
              left: 0;
              text-align: center;
            }
          }
        }

        .button {
          height: 46px;
          min-height: 46px;
          line-height: 46px;
          font-size: 13px;
          color: #888888;
          position: relative;

          .red-dot {
            position: absolute;
            display: inline-block;
            width: 4px;
            height: 4px;
            background: #EF4F2A;
            border-radius: 50%;
            top: 14px;
            right: 16px;
          }

          &.router-link-active {
            background-color: #FFF;
            color: #3A3A3A;
          }
        }
      }
    }
  }

</style>
<script>
  export default {
    props: {
      links: Array,
      numbered: {
        type: Boolean,
        default: false
      },

      barWidth: {
        type: Number,
        required: true
      },
      // page content bg color
      bgColor: {
        type: String,
        default: '#f5f5f5'
      },

      onInit: Function,
      onTabClick: Function,
      useContentSlot: {
        type: String,
        default: 'false'
      },
      hasNew: {
        type: Boolean,
        default: false
      }
    },

    methods: {
      onClick (index) {
        if (this.onTabClick) this.onTabClick(index)
      }
    }
  }
</script>



